import IndexModel from '@/models/index'
import { checkMobile } from '@/util/utils'
export default {
	data() {
		return {
			title: 'Hello1',
			list: [],
			form: {
				num1: '',
				num2: '',
				tel: ''
			},
			dialogFlag: false
		}
	},
	onLoad() {
	},
	methods: {
		async onSubmit() {
			
			const duration = 2000
			if (!this.form.num1) {
				uni.showToast({
				    title: '请输入工频空压机台数',
					icon: 'none',
				    duration
				})
				return
			}
			if (!this.form.num2) {
				uni.showToast({
				    title: '请输入变频空压机台数',
					icon: 'none',
				    duration
				})
				return
			}
			if (!checkMobile(this.form.tel)) {
				uni.showToast({
				    title: '请输入正确的手机号',
					icon: 'none',
				    duration
				})
				return
			}
			await IndexModel._submitForm({
				compressorss_number: this.form.num1,
				conversion_number: this.form.num2,
				phone: this.form.tel,
			})
			this.onClearForm()
			this.onShowDialog()
		},
		onClearForm() {
			this.form.num1 = ''
			this.form.num2 = ''
			this.form.tel = ''
		},
		onShowDialog() {
			this.dialogFlag = true
		},
		onHideDialog() {
			this.dialogFlag = false
		},
		async getUserInfo(e) {
			
			// 先走授权
			console.log(e)
			const { errMsg, userInfo } = e.detail
			console.log(userInfo)
			
			// 失败不管他
			if (errMsg != 'getUserInfo:ok') {
				uni.showToast({
				    title: '授权失败',
					icon: 'none',
				    duration: 2000
				})
				return
			}
			
			await IndexModel._submitUserInfo({
				nickname: userInfo.nickName,
				gender: userInfo.gender === 0 ? '未知' : userInfo.gender === 1 ? '男' : '女',
				user_avator: userInfo.avatarUrl,
				country: userInfo.country,
				province: userInfo.province,
				city: userInfo.city
			})
			uni.navigateTo({
			    url: `/pages/webView/webView?src=${encodeURIComponent('https://mp.weixin.qq.com/s/xU8FOaGlF0tqK_b5VGV1iQ')}`
			})
		},
		onCallTel() {
			uni.makePhoneCall({
			    phoneNumber: '400-086-2020' //仅为示例
			});
		}
	}
}
